import { instituteFeedbackApi } from "./instituteFeedbackApi";
import {
  feedbackCreateStudentFeedbackFunction,
  feedbackDetailStudentFeedbackFunction,
  feedbackGiveStudentFeedbackFunction,
  feedbackRemoveStudentFeedbackFunction,
  feedbackStudentFeedbackListFunction,
  feedbackSubjectTeacherListFunction,
  feedbackUpdateStudentFeedbackFunction,
  feedbackQuestionCreateFunction,
  feedbackQuestionUpdateFunction,
  feedbackQuestionRemoveFunction,
} from "./institute_feedback_query_mutation_function";

const instituteFeedbackApiDynamic = instituteFeedbackApi.injectEndpoints({
  endpoints: (builder) => ({
    feedbackStudentFeedbackList: feedbackStudentFeedbackListFunction(builder),
    feedbackSubjectTeacherList: feedbackSubjectTeacherListFunction(builder),
    feedbackCreateStudentFeedback:
      feedbackCreateStudentFeedbackFunction(builder),
    feedbackDetailStudentFeedback:
      feedbackDetailStudentFeedbackFunction(builder),
    feedbackUpdateStudentFeedback:
      feedbackUpdateStudentFeedbackFunction(builder),
    feedbackRemoveStudentFeedback:
      feedbackRemoveStudentFeedbackFunction(builder),
    feedbackGiveStudentFeedback: feedbackGiveStudentFeedbackFunction(builder),

    feedbackQuestionCreate: feedbackQuestionCreateFunction(builder),
    feedbackQuestionUpdate: feedbackQuestionUpdateFunction(builder),
    feedbackQuestionRemove: feedbackQuestionRemoveFunction(builder),
    // feedbackQuestionRemoveFunction:feedbackQuestionRemoveFunction(builder),
    // feedbackQuestionRemoveFunction:feedbackQuestionRemoveFunction(builder),
    // feedbackQuestionRemoveFunction:feedbackQuestionRemoveFunction(builder),
    // feedbackQuestionRemoveFunction:feedbackQuestionRemoveFunction(builder),
  }),
});

export const {
  useFeedbackStudentFeedbackListQuery,
  useFeedbackSubjectTeacherListQuery,
  useFeedbackCreateStudentFeedbackMutation,
  useFeedbackDetailStudentFeedbackQuery,
  useFeedbackUpdateStudentFeedbackMutation,
  useFeedbackRemoveStudentFeedbackMutation,
  useFeedbackGiveStudentFeedbackMutation,
  useFeedbackQuestionCreateMutation,
  useFeedbackQuestionUpdateMutation,
  useFeedbackQuestionRemoveMutation,
} = instituteFeedbackApiDynamic;
